/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
/* RESET */
.wp-core-ui select {
  font-size: revert;
  line-height: revert;
}

/* STYLE */
html :where(.wp-block-formello-form) input[type],
html :where(.wp-block-formello-form) select,
html :where(.wp-block-formello-form) textarea[readonly] {
  background-color: #fff;
  outline: none;
  box-sizing: content-box;
  margin: 0;
}
html :where(.wp-block-formello-form) input[type=date] {
  padding: 5px;
}
html :where(.wp-block-formello-form) select[readonly]:hover {
  color: #000;
}
html :where(.wp-block-formello-form) button {
  color: #000;
  border: none;
  font-size: 1rem;
}

.formello-fieldset-options {
  padding: 1em;
  border: 1px solid #ddd;
  margin-bottom: 1em;
}

.formello-urlinput input {
  width: 100% !important;
  border-radius: 2px !important;
  border: 1px solid #757575 !important;
}

.formello-hidden {
  display: flex;
  align-items: center;
}

.wp-block-formello-form.as-row .formello-hidden {
  margin-left: 160px;
}

.wp-block-formello-form label.required::after {
  content: " *";
  color: red;
}

.wp-block-formello-button {
  display: block;
}
.wp-block-formello-button.left {
  margin-left: 0;
}
.wp-block-formello-button.right {
  margin-right: 0;
}
.wp-block-formello-button.center {
  margin: auto;
}
.wp-block-formello-button.wide {
  width: 100%;
}

/* MODAL */
.formello-modal-footer {
  padding: 1em 0;
  text-align: right;
}

.hide {
  visibility: hidden;
}